<template>
  <div class="wrap">
    <div class="search_form">
      <!-- <div class="logo"><img src="img/logo.png" alt="logo" /></div> -->
      <div class="form_group">
        <input
          type="text"
          v-model="city"
          class="input_txt"
          placeholder="请输入查询的天气"
        />
        <button class="input_sub" @click="searchWeather">
          搜 索
        </button>
      </div>
      <div class="title" style="color:#fff;font-size:20px">
        热门城市
      </div>
      <div class="hotkey">
        <a href="javascript:;" @click="changeCity('北京')">北京</a>
        <a href="javascript:;" @click="changeCity('上海')">上海</a>
        <a href="javascript:;" @click="changeCity('广州')">广州</a>
        <a href="javascript:;" @click="changeCity('深圳')">深圳</a>
        <a href="javascript:;" @click="changeCity('成都')">成都</a>
      </div>
      <div class="hotkey">
        <a href="javascript:;" @click="changeCity('河北')">河北</a>
        <a href="javascript:;" @click="changeCity('河南')">河南</a>
        <a href="javascript:;" @click="changeCity('吉林')">吉林</a>
        <a href="javascript:;" @click="changeCity('重庆')">重庆</a>
        <a href="javascript:;" @click="changeCity('长沙')">长沙</a>
      </div>
      <div class="hotkey">
        <a href="javascript:;" @click="changeCity('乌鲁木齐')">乌鲁木齐</a>
        <a href="javascript:;" @click="changeCity('苏州')">苏州</a>
        <a href="javascript:;" @click="changeCity('佛山')">佛山</a>
        <a href="javascript:;" @click="changeCity('南宁')">南宁</a>
        <a href="javascript:;" @click="changeCity('西安')">西安</a>
      </div>
      <div class="hotkey">
        <a href="javascript:;" @click="changeCity('临汾')">临汾</a>
        <a href="javascript:;" @click="changeCity('秦皇岛')">秦皇岛</a>
        <a href="javascript:;" @click="changeCity('天津')">天津</a>
        <a href="javascript:;" @click="changeCity('青岛')">青岛</a>
        <a href="javascript:;" @click="changeCity('武汉')">武汉</a>
      </div>
      <div v-show="isselect" class="custom_container"></div>
      <div v-show="isselect" class="wuCity">
        当前无{{ this.city }}信息
        <div
          class="xxx"
          @click="
            isselect = !isselect;
            city = '';
          "
        >
          X
        </div>
      </div>
      <ul class="index_bottom">
        <li>
          <img src="../img/tianqi.png" @click="gotoSwiper" />
        </li>
        <li>
          <router-link to="/search">
            <img src="../img/sangediandian.png" />
          </router-link>
        </li>
        <li>
          <router-link to="/my">
            <img src="../img/sangediandian_1.png"
          /></router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { Dialog } from "vant";
export default {
  data() {
    return {
      city: "",
      weatherdata: {},
      forecast: [],
      forecast2: [],
      isselect: false,
    };
  },
  components: {},
  mounted() {},
  methods: {
    async searchWeather() {
      var that = this;
      await axios
        .get("http://wthrcdn.etouch.cn/weather_mini?city=" + this.city)
        .then(function(response) {
          // console.log(response);
          console.log(response.data.data);
          that.weatherdata = response.data.data; //.forecast.slice(0, 1)
          that.forecast = response.data.data.forecast.slice(0, 1);
          that.forecast2 = response.data.data.forecast.slice(
            1,
            response.data.data.forecast.length - 1
          );
        })
        .catch(function(err) {
          if (err) {
            that.isselect = true;
            console.log(err);
          }
        });
      if (this.isselect) {
        return false;
      } else {
        this.$router.push(`/find/${that.city}`);
      }
    },
    changeCity: function(city) {
      this.city = city;
      //   this.searchWeather();
    },
    gotoSwiper() {
      if (this.$store.state.weatehrCityList.length == 0) {
        Dialog({ message: "暂无自选城市，快去添加吧" });
      } else {
        this.$router.push("/swiper");
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.wrap {
  background: url("../img/bg.jpg");
  background-size: 100% 100%;
}

.wrapdy {
  background: url("../img/duoyun.jpg");
  background-size: 100% 100%;
}
.wrapwm {
  background: url("../img/wumai.jpg");
  background-size: 100% 100%;
}
.wrapwy {
  background: url("../img/xiayu.jpg");
  background-size: 100% 100%;
  // color: #fff;
}
h2 {
  color: #fff;
}
.weathertype {
  position: absolute;
  right: 20px;
  top: 30px;
  font-size: 30px;
  color: #fff;
}

.weatherfeng {
  position: absolute;
  right: 10px;
  top: 45px;
  font-size: 14px;
  color: #fff;
}

.wuCity {
  position: relative;
  text-align: center;
  width: 50%;
  padding: 25px;
  border-radius: 10px;
  top: 30%;
  z-index: 99;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -o-transform: translateX(-50%);
  transform: translateX(-50%);
  font-size: 20px;
  color: #333;
  background: rgba(245, 245, 245, 1);
  .xxx {
    position: absolute;
    right: 5px;
    top: 5px;
  }
  z-index: 999;
}

.custom_container {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
  background: rgba(0, 0, 0, 0.3);
}
</style>
