<template>
  <div class="wrap fengmian">
    <h3>倒计时{{ num }}秒后跳转</h3>
    <h2>
      Welcome
    </h2>
    <!-- 定时器跳转 -->
  </div>
</template>

<script>
export default {
  data() {
    return {
      num: 3,
      timer: null,
    };
  },
  mounted() {
    this.gotoIndex();
  },
  components: {},
  methods: {
    gotoIndex() {
      this.timer = setInterval(() => {
        this.num--;
        if (this.num == 0) {
          clearInterval(this.timer);
          this.$router.push("/search");
        }
      }, 1000);
    },
  },
};
</script>

<style lang="scss" scoped>
.fengmian {
  background: url("../img/fengmian.jpg");
  background-size: 100% 100%;
}
h2 {
  position: absolute;
  left: 50%;
  top: 40%;
  font-size: 40px;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -o-transform: translateX(-50%);
  transform: translateX(-50%);
  color: #fff;
  opacity: 0.1;
  animation: animated_div 3s;
  -moz-animation: animated_div 3s; /* Firefox */
  -webkit-animation: animated_div 3s; /* Safari 和 Chrome */
  -o-animation: animated_div 3s;
}
h3 {
  position: absolute;
  top: 4%;
  right: 6%;
  font-size: 14px;
  color: #fff;
  padding: 5px 15px;
  background: rgba($color: #fff, $alpha: 0.2);
  border-radius: 20px;
}
@keyframes animated_div {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
</style>
