import Vue from 'vue'
import Router from 'vue-router'
// //引入组件
import Index from '../views/Index.vue'
import AddWeather from '../components/AddWeather.vue'
import WeatherInfo from '../components/WeatherInfo.vue'
import Welcome from '../components/Welcome.vue'
import My from '../components/My.vue'
import Swiper from '../components/Swiper.vue'
import Search from '../components/FindWeather.vue'










// 使用插件
Vue.use(Router)
const routes = [
    { path: '/', redirect: "/welcome" },
    { path: '/find/:name', component: Search },
    { path: '/addweather', component: AddWeather },
    { path: '/waterinfo/:id', component: WeatherInfo },
    { path: '/welcome', component: Welcome },
    { path: '/my', component: My },
    { path: '/swiper', component: Swiper },
    { path: '/search', component: Index },


];
const router = new Router({
    routes,
    mode: 'hash',
    // 打开会导致打包路由失败
    base: process.env.BASE_URL,
})
export default router