import Vue from "vue";
import Vuex from "vuex";
import VuexPersist from 'vuex-persist'
Vue.use(Vuex);

const persist = new VuexPersist({
  //本地存储仓库  设置state数据存放到window.sessionStorage
  storage: window.localStorage
})


//配置vuex
export default new Vuex.Store({
  //plugins数组，表示可以安装很多插件
  plugins: [persist.plugin],
  // 开启严格模式 
  strict: true,
  //根状态
  state: {
    //sub 附属页面
    navFirst: "Firstpage",
    weatehrCityList: [{ "city": "北京", "date": "16日星期二", "fengli": "<![CDATA[1级]]>", "fengxiang": "西南风", "high": "高温 11℃", "low": "低温 2℃", "type": "晴", "wendu": "8" }]
  },
  //同步执行
  mutations: {
    setList(state, data) {
      state.navFirst = data;
      // VuexPersist
    },
    addweatehrCityList(state, data) {
      state.weatehrCityList.push(data)
    },
    delweatehrCity(state, data) {
      state.weatehrCityList.splice(data, 1)
    }

  },

})