<template>
  <div>
    <van-nav-bar
      title="城市列表"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <ul>
      <li v-for="(item, index) in citylist" :key="index">
        <div class="one">
          <div>
            {{ item.city }} {{ item.wendu }} °C
            <div style="font-size:16px">~天气：{{ item.type }}</div>
          </div>
          <div class="left" style="font-size:18px" @click="gotoInfo(item.city)">
            查看详情
          </div>
          <div class="del" @click="delone(item.city, index)">删除</div>
        </div>
      </li>
    </ul>
    <div class="tishi" v-if="citylist.length == 0">
      暂无信息，快去添加信息吧
    </div>
  </div>
</template>

<script>
import { NavBar } from "vant";
import { Dialog } from "vant";
export default {
  data() {
    return {
      citylist: [],
    };
  },
  mounted() {
    this.getcitylist();
  },
  components: {},
  methods: {
    getcitylist() {
      console.log(this.$store.state);
      this.citylist = this.$store.state.weatehrCityList;
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    gotoInfo(city) {
      this.$router.push(`/waterinfo/${city}`);
    },
    delone(city, idx) {
      Dialog.confirm({
        title: "提示",
        message: `确定要删除${city}信息吗`,
      })
        .then(() => {
          // on confirm
          this.$store.commit("delweatehrCity", idx);
          Dialog({ message: "删除成功" });
        })
        .catch(() => {
          // on cancel
        });
    },
  },
};
</script>

<style lang="scss" scoped>
li {
  width: 90%;
  box-sizing: border-box;
  height: 70px;
  font-size: 20px;
  margin: 5px auto 5px;
  padding: 5px;
  border-bottom: 1px #cfcfcf solid;
  .one {
    display: flex;
    justify-content: space-around;
    align-items: center;
    .left {
      div {
        display: inline-block;
      }
    }
    .del {
      font-size: 16px;
      padding: 2px 10px;
      border-radius: 5px;
      background: indianred;
      color: #fff;
      height: 25px;
      box-sizing: border-box;
    }
  }
}
.tishi {
  width: 80%;
  font-size: 20px;
  margin: 20px auto 20px;
}
</style>
