<template>
  <div class="wrapinfo" :class="wetherWrap ? wetherWrap : 'wrapinfo'">
    <div class="search_form" style="margin: 0 auto 0;padding:20px 0 0 0">
      <ul class="weather_list" style="margin-top:0">
        <li v-for="(item, index) in forecast" :key="index">
          <h2 style="margin:20px 0 5px;">
            {{ weatherdata.city ? weatherdata.city : "....加载中" }}
          </h2>
          <div class="weathertype">{{ item.type }}</div>
          <div class="info_temp">当前温度</div>
          <div class="info_temp" style="font-size: 30px;">
            {{ weatherdata.wendu }} °C
          </div>
          <div class="info_type"></div>
          <div class="info_temp">
            <b>{{ item.low }}</b>
            ~
            <b>{{ item.high }}</b>
          </div>
        </li>
      </ul>
      <ul class="all_list" v-show="forecast2.length > 0">
        <li
          v-for="(item, index) in forecast2"
          :key="index"
          style="display:flex;justify-content:space-evenly"
        >
          <div>{{ item.date.slice(3, 6) }}-{{ item.type }}</div>
          <div>—</div>
          <div>{{ item.low.slice(2, 9) }} ~ {{ item.high.slice(2, 9) }}</div>
        </li>
      </ul>

      <ul class="about_list">
        <li @click="dressingIndex">穿衣指数<img src="../img/clothes.png" /></li>
        <li @click="washIndex">
          洗车指数<img src="../img/qichepeijian.png" />
        </li>
        <li @click="ganmaoIndex">感冒指数<img src="../img/keaigui10.png" /></li>
        <li @click="activeIndex">运动指数<img src="../img/yundong.png" /></li>
        <li @click="ziwaixIndex" style="font-size:12px">
          紫外线指数<img src="../img/taiyang.png" />
        </li>
      </ul>
      <ul
        class="index_bottom"
        style="box-sizing: border-box;width:320px;justify-content: space-around;"
      >
        <li>
          <router-link to="/search">
            <img src="../img/sangediandian.png" />
          </router-link>
        </li>
        <li>
          <router-link to="/my">
            <img src="../img/sangediandian_1.png" />
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { NavBar } from "vant";
import { Dialog } from "vant";

export default {
  data() {
    return {
      city: "",
      weatherdata: {},
      forecast: [],
      forecast2: [],
      wetherWrap: "",
    };
  },
  mounted() {
    this.city = this.$store.state.weatehrCityList[3].city;
    this.searchWeather();
  },
  methods: {
    getweather() {
      let type = this.forecast[0].type;
      let obj = {
        晴: "wrapinfo",
        多云: "wrapdy",
        霾: "wrapwm",
        小雨: "wrapwy",
        阴: "wrapdy",
      };
      console.log(obj[type]);
      this.wetherWrap = obj[type];
    },

    async searchWeather() {
      var that = this;
      await axios
        .get("http://wthrcdn.etouch.cn/weather_mini?city=" + this.city)
        .then(function(response) {
          // console.log(response);
          console.log(response.data.data);
          that.weatherdata = response.data.data; //.forecast.slice(0, 1)
          that.forecast = response.data.data.forecast.slice(0, 1);
          that.forecast2 = response.data.data.forecast.slice(
            1,
            response.data.data.forecast.length - 1
          );
        })
        .catch(function(err) {});

      // Dialog({ message: this.weatherdata.ganmao });
      this.getweather();
    },
    changeCity: function(city) {
      this.city = city;
      this.searchWeather();
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    dressingIndex() {
      if (this.weatherdata.wendu < 5) {
        Dialog({ message: "注意保暖，乖乖穿袄吧" });
      } else {
        Dialog({ message: "加厚衣服哦" });
      }
      console.log(this.weatherdata);
    },
    washIndex() {
      if (this.weatherdata.wendu < 5) {
        Dialog({ message: "都快冻冰了，等温度高一点吧" });
      } else {
        Dialog({ message: "当前可以洗车哦" });
      }
    },
    ganmaoIndex() {
      Dialog({ message: this.weatherdata.ganmao });
    },
    activeIndex() {
      if (this.weatherdata.wendu < 5) {
        Dialog({ message: "室内运动更合适哦" });
      } else {
        Dialog({ message: "加厚衣服再运动哦" });
      }
    },
    ziwaixIndex() {
      Dialog({ message: "注意防晒" });
    },
  },
};
</script>

<style lang="scss" scoped>
.wrapinfo {
  width: 100%;
  height: 100%;
}
.wrapinfo {
  background: url("../img/bg.jpg");
  background-size: 100% 100%;
}
.wrapdy {
  background: url("../img/duoyun.jpg");
  background-size: 100% 100%;
}
.wrapwm {
  background: url("../img/wumai.jpg");
  background-size: 100% 100%;
}
.wrapwy {
  background: url("../img/xiayu.jpg");
  background-size: 100% 100%;
  // color: #fff;
}
.weathertype {
  position: absolute;
  right: 20px;
  top: 100px;
  font-size: 20px;
  color: #fff;
}
h2 {
  color: #fff;
  font-size: 30px;
}
.info_temp {
  color: #fff;
}

.swiper-slide {
  height: 600px;
}
</style>
