<template>
  <div class="swiper-container">
    <van-nav-bar
      title="天气详情"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <div class="swiper-wrapper">
      <div
        class="swiper-slide"
        ref="homePage"
        v-for="(item, index) in swiperList.slice(
          0,
          $store.state.weatehrCityList.length
        )"
        :key="index"
      >
        <keep-alive>
          <component :is="item.component"></component>
        </keep-alive>
      </div>
      <div class="tishi" v-if="datalist.length == 0">
        暂无信息，快去添加信息吧
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { NavBar } from "vant";
import { Dialog } from "vant";

import Swiper from "swiper";
import WetherChild from "./WeatherChild.vue";
import WetherChild2 from "./WeatherChild2.vue";
import WetherChild3 from "./WeatherChild3.vue";
import WetherChild4 from "./WeatherChild4.vue";
import WetherChild5 from "./WeatherChild5.vue";

export default {
  data() {
    return {
      city: "河北",
      weatherdata: {},
      forecast: [],
      forecast2: [],
      wetherWrap: "",
      numIndex: 0,
      datalist: [],
      swiperList: [
        { component: WetherChild },
        { component: WetherChild2 },
        { component: WetherChild3 },
        { component: WetherChild4 },
        { component: WetherChild5 },
      ],
      clientHeight: "",
    };
  },
  components: {
    WetherChild,
    WetherChild2,
    WetherChild3,
    WetherChild4,
    WetherChild5,
  },

  mounted() {
    this.clientHeight = `${document.documentElement.clientHeight}`;
    let that = this;
    window.onresize = function temp() {
      that.clientHeight = `${document.documentElement.clientHeight}`;
    };

    this.city = "北京";
    this.datalist = this.$store.state.weatehrCityList;
    this.searchWeather();
    this.getSwiper();
  },
  watch: {
    // 如果 `clientHeight` 发生改变，这个函数就会运行
    clientHeight: function() {
      this.changeFixed(this.clientHeight);
    },
  },

  methods: {
    changeFixed(clientHeight) {
      //动态修改样式
      console.log(this.$refs);
      for (let i = 0; i < this.$store.state.weatehrCityList.length; i++) {
        this.$refs.homePage[i].style.height = clientHeight - 46 + "px";
      }
    },

    getSwiper() {
      let that = this;
      var mySwiper = new Swiper(".swiper-container", {
        on: {
          touchEnd: function(event) {
            console.log("触摸停止", event, event.activeIndex);
            // getDecoration();
          },
          slidePrevTransitionEnd: function() {
            console.log("pre");
            // that.city = "上海";
            // if (that.numIndex < 5) {
            //   that.numIndex++;
            //   that.city = that.swiperList[that.numIndex].city;
            //   that.searchWeather();
            // } else {
            //   return false;
            // }
            // console.log(that.numIndex);
          },
          slideNextTransitionEnd: function() {
            console.log("next");
            // that.city = "广州";
            // if (that.numIndex > 0) {
            //   that.numIndex--;
            //   that.city = that.swiperList[that.numIndex].city;
            //   that.searchWeather();
            // } else {
            //   return false;
            // }
          },
        },
      });
    },
    getweather() {
      let type = this.forecast[0].type;
      let obj = {
        晴: "wrapinfo",
        多云: "wrapdy",
        霾: "wrapwm",
        小雨: "wrapwy",
        阴: "wrapdy",
      };
      console.log(obj[type]);
      this.wetherWrap = obj[type];
    },

    async searchWeather() {
      var that = this;
      await axios
        .get("http://wthrcdn.etouch.cn/weather_mini?city=" + this.city)
        .then(function(response) {
          // console.log(response);
          console.log(response.data.data);
          that.weatherdata = response.data.data; //.forecast.slice(0, 1)
          that.forecast = response.data.data.forecast.slice(0, 1);
          that.forecast2 = response.data.data.forecast.slice(
            1,
            response.data.data.forecast.length - 1
          );
        })
        .catch(function(err) {});

      // Dialog({ message: this.weatherdata.ganmao });
      this.getweather();
    },
    changeCity: function(city) {
      this.city = city;
      this.searchWeather();
    },
    onClickLeft() {
      this.$router.go(-1);
    },
  },
};
</script>

<style lang="scss" scoped>
.wrapinfo {
  width: 100%;
  height: 100%;
}
.wrapinfo {
  background: url("../img/bg.jpg");
  background-size: 100% 100%;
}
.wrapdy {
  background: url("../img/duoyun.jpg");
  background-size: 100% 100%;
}
.wrapwm {
  background: url("../img/wumai.jpg");
  background-size: 100% 100%;
}
.wrapwy {
  background: url("../img/xiayu.jpg");
  background-size: 100% 100%;
  // color: #fff;
}
.weathertype {
  position: absolute;
  right: 20px;
  top: 100px;
  font-size: 20px;
  color: #fff;
}
h2 {
  color: #fff;
  font-size: 30px;
}
.info_temp {
  color: #fff;
}

.tishi {
  width: 80%;
  font-size: 20px;
  margin: 20px auto 20px;
}
</style>
